//img.info >img + img-info-background > img-content
.img-info {
    position: relative;
    &-background {
        display: block;
        opacity: 0;
        transition: .3s;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 8px 12px;
       
        @include md{
            padding: 16px;
        }
    }
    &-content {
      color: $white;
      height: 100%;
      overflow: hidden;
  
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    &:hover &-background {
        opacity: 1 !important;
        transition: .3s;
      }
  }

  .img-top{
    transition: all 0.3s;
    &:hover{
        transform: scale(1.1);
    }
  }

  